    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: auto;

}
h4{
    margin-left: 25%;

}
button{
    margin-left: 35%;
}
*{
    padding:0px;
    margin:0px;
}

.one{
    display: flex;
    flex-direction: row;
   
  
  }

.searchbar{
    
    height:auto;
    float: left;
    width:40%;
    margin-left:1%;
    margin-top: 1%;
   


}
.footer{
   
    height:70px;
    display: flex;
    flex-direction: row;
    justify-content:space-around;
    background:white;
    
  }
.searchbar input {
    
    border-radius: 5px;
    font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif; 
}

.sessionout{
    width:10%;
    height:40px;
    float:right;
    margin-left:45%;
    margin-top: 2%;
    background-color: white;
    border-radius: 5px;
    cursor: pointer;
    

}
button{
    background-color: dodgerblue;
    cursor: pointer;
    color:white;
}
button:hover{
    background-color: dodgerblue;
}
.logout{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: whitesmoke;
    height:600px;
    
}
